import "./css/Navbar.css"
import logo from "./assets/logo-white.png"
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import SearchRoundedIcon from '@mui/icons-material/SearchRounded';
import PersonIcon from '@mui/icons-material/Person';
import MenuIcon from '@mui/icons-material/Menu';
import OutsideClickHandler from 'react-outside-click-handler';


export default function Navbar() {
    let [accountVis, setAccountVis] = useState(0)
    let [menuVis, setMenuVis] = useState(0)
    let [search, setSearch] = useState("")
    const [timeout, setTime] = useState(null);
    const [animeData, setAnimeData] = useState([]);

    const handleKeyUp = (e) => {
        clearTimeout(timeout);
        setTime(setTimeout(function () {
            searchAnime(search);
        }, 200))
    };

    function searchAnime(search) {
        fetch(`https://backend-server-ao58.onrender.com/search?keyw=${search}`).then(
            response => response.json()
        ).then(
            data => {
                search === "" ? setAnimeData([]) : setAnimeData(data);
            }
        )
    }

    useEffect(() => {
        searchAnime(search);
    }, [search])


    return (
        <>
            <div
                className="navbar">
                <div
                    className="logo">
                    <Link
                        to="/">
                        <img
                            className="logo-img"
                            src={logo}
                            alt="logo"
                        />
                    </Link>
                </div>
                <div
                    className="navButtons">
                    {/* <li>
                        <Link
                            to="/movies">
                            MOVIE
                        </Link>
                    </li>
                    <li>
                        <Link
                            to="/tv-shows">
                            TV SHOW
                        </Link>
                    </li> */}
                    <OutsideClickHandler
                        onOutsideClick={() => {
                            setSearch("")
                        }}
                    >
                        <li>
                            {/* {searchVis ?
                                <>
                                    <input
                                        className="animeName"
                                        id="animeName"
                                        placeholder="Search"
                                        value={search}
                                        onChange={(e) => setSearch(e.target.value)}
                                        onKeyUp={handleKeyUp}
                                        required />
                                    <div className="search-result">
                                        {animeData.map((elem, i) => <Card key={i} animeDetails={elem} />)}
                                    </div>
                                </> : <> </>} */}
                            <Link to="/search">
                                <SearchRoundedIcon />
                            </Link>
                        </li>
                    </OutsideClickHandler>
                    {/* <li className="account">
                        <Link
                            to={`/account`}>
                            <PersonIcon
                                className="user-account"
                                onClick={() => setAccountVis(!accountVis)} />
                        </Link>
                    </li> */}
                </div>
            </div>
            <div className="navbar-mobile">
                <div
                    className="logo">
                    <Link
                        to="/">
                        <img
                            className="logo-img"
                            src={logo}
                            alt="logo"
                        />
                    </Link>
                </div>
                <OutsideClickHandler
                    onOutsideClick={() => {
                        setMenuVis(0);
                    }}
                >
                    <div className="account">
                        <MenuIcon
                            className="menu-button"
                            onClick={() => setMenuVis(!menuVis)}
                        />
                        {menuVis ? <>
                            <div className="menu">
                                <li>
                                    <Link
                                        to="/search">
                                        SEARCH
                                    </Link>
                                </li>
                                {/* <li>
                                    <Link
                                        to="/movies">
                                        MOVIE
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to="/tv-shows">
                                        TV SHOW
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to="/account">
                                        ACCOUNT
                                    </Link>
                                </li> */}
                            </div>
                        </> : <> </>}
                    </div>
                </OutsideClickHandler>
            </div>
        </>
    )
}