import { useEffect, useState } from "react";
import Hls from "hls.js";
import Plyr from "plyr";
import $ from 'jquery';


export default function Video(props) {
    const [visibility, setVisibility] = useState('hidden');
    const [anime, setAnime] = useState({});
    const [isData, setIsData] = useState(false);
    const [isNext, setIsNext] = useState(false);
    const [isPrev, setIsPrev] = useState(false);

    const slug = props.slug;
    const episodeNum = props.episodeNum;

    function loadVideo(url, next, prev, isNext, isPrev) {
        var video = document.querySelector("#player")
        if (Hls.isSupported()) {
            var hls = new Hls();
            hls.loadSource(url);
            hls.attachMedia(video);
            hls.on(Hls.Events.MANIFEST_PARSED, () => {
                video.play();
                loadPlayer(next, prev, isNext, isPrev)
            });
        }

        function updateQuality(newQuality) {
            hls.levels.forEach((level, levelIndex) => {
                if (level.height === newQuality) {
                    hls.currentLevel = levelIndex;
                }
            });
        }

        function loadPlayer(next, prev, isNext, isPrev) {
            let defaultOptions = {controls: ['play-large', 'rewind', 'play', 'fast-forward', 'progress', 'current-time', 'duration', 'mute', 'volume', 'pip', 'settings', 'fullscreen']};
            const availableQualities = hls.levels.map((l) => l.height)
            defaultOptions.quality = {
                default: availableQualities[1],
                options: availableQualities,
                forced: true,
                onChange: (e) => updateQuality(e),
            }

            const player = new Plyr('#player', defaultOptions);
            player.on('enterfullscreen', () => {
                window.screen.orientation.lock('landscape');
            });
            
            player.on('exitfullscreen', () => {
                window.screen.orientation.lock('portrait');
            });
            if (prev && !isPrev) {
                setIsPrev(true);
                $(`<button type="button" onclick="location.href = './${Number(episodeNum) - 1}'" class="plyr-prev"><i class="fa fa-step-backward fa-lg"></i></button>`).insertBefore('.plyr__controls [data-plyr="pip"]');
            }
            if (next && !isNext) {
                setIsNext(true);
                $(`<button type="button" onclick="location.href = './${Number(episodeNum) + 1}'" class="plyr-next"><i  class="fa fa-step-forward fa-lg"></i></button>`).insertBefore('.plyr__controls [data-plyr="pip"]');
            }
            return player;
        }
    }

    function getLink(slug, episodeNum) {
        fetch(`https://backend-server-ao58.onrender.com/scrapemp4?slug=${slug}&episode=${episodeNum}`).then(
            response => response.json()
        ).then(
            data => {
                setAnime({
                    animeLink: data.sources_bk[0].file,
                    next: data.next,
                    prev: data.prev
                });
                setIsData(true);
                setVisibility("visible");
            }
        )
    }

    useEffect(() => {
        getLink(slug, episodeNum)
    }, [slug, episodeNum])


    return (
        <>
            {isData ? <>
                {loadVideo(anime.animeLink, anime.next, anime.prev, isNext, isPrev)}
            </> : <>
            </>
            }
            <div className="anime-video" style={{ visibility: `${visibility}` }}>
                <video preload='none' id='player' autoPlay controls allowFullScreen></video>
            </div>
        </>
    )
}

