import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import Navbar from "./Navbar";
import "./css/Details.css";

export default function Details() {
    const [animeData, setAnimeData] = useState([]);
    const [isData, setIsData] = useState(false);

    const params = useParams();
    const slug = params.slug;

    function searchAnime(slug) {
        fetch(`https://backend-server-ao58.onrender.com/animedetails?slug=${slug}`).then(
            response => response.json()
        ).then(
            data => {
                setAnimeData(data);
                setIsData(true);
            }
        )
    }

    useEffect(() => {
        searchAnime(slug)
    }, [slug])

    return (
        <>
            <Navbar />

            {isData ? <>
                <div className="anime-details">
                    <div className="common-details">
                        <div className="image-div">
                            <img
                                src={animeData.animeImg}
                                alt={`${slug}-img`}
                            />
                        </div>
                        <div className="details">
                            <h1>{animeData.animeTitle}</h1>
                            <br></br>
                            <h3 className="info">Type:</h3>
                            <h5>{animeData.type}</h5>
                            <br></br>
                            <h3 className="info">Release Data:</h3>
                            <h5>{animeData.releasedDate}</h5>
                            <br></br>
                            <h3 className="info">Status: </h3>
                            <h5>{animeData.status}</h5>
                            <br></br>
                            <h3 className="info">Genre: </h3>
                            {animeData.genres.map((e, i) => <h6 className="genre" key={i}>{e}</h6>)}
                            <br></br>
                            <h3 className="info">Other Names:</h3>
                            <h5>{animeData.otherNames}</h5>
                            <br></br>
                            <h3 className="info">Episode Number:</h3>
                            <h5>{animeData.totalEpisodes}</h5>
                            <br></br>
                            <h3 className="info">Synopsis:</h3>
                            <h5 className="synopsis">{animeData.synopsis}</h5>
                        </div>
                    </div>
                    <div className="all-episodes">
                        {animeData.episodesList.map((elem, i) => {
                            return (
                                <Link key={i} to={`./episode/${elem.episodeNum}`}>
                                    <div  className={`${elem.episodeId}`}>
                                        <h3>{elem.episodeNum}</h3>
                                    </div>
                                </Link>
                            )
                        })}
                    </div>
                </div>
            </> : <>
                <h1>Loading..</h1>
            </>
            }
        </>
    )
}

