import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import Navbar from "./Navbar";

import "./css/Search.css"

export default function Details() {
    const [animeList, setAnimeList] = useState([]);
    const [visNext, setVisNext] = useState("visible");
    const [visPrev, setVisPrev] = useState("visible");
    const [isData, setIsData] = useState(false);

    const params = useParams();
    const keyw = params.keyw;
    const page = params.page;


    function searchAnime(keyw, page) {
        page <= 1 ? setVisPrev("hidden") : setVisPrev("visible")
        fetch(`https://backend-server-ao58.onrender.com/search?keyw=${keyw}&page=${page}`).then(
            response => response.json()
        ).then(
            data => {
                setAnimeList(data);
                setIsData(true);
            }
        )
        fetch(`https://backend-server-ao58.onrender.com/search?keyw=${keyw}&page=${Number(page) + 1}`).then(
            response => response.json()
        ).then(
            data => {
                if (data.length === 0 ) {
                    setVisNext("hidden")
                }
                else{
                    setVisNext("visible")
                }
            }
        )
    }

    useEffect(() => {
        searchAnime(keyw, page)

    }, [keyw, page])

    return (
        <>
            <Navbar />
            <div className="pages">
                <Link className="prev-page" style={{ visibility: `${visPrev}` }} to={`/search/${keyw}/${Number(page) - 1}`}>
                    <h3>Previous</h3>
                </Link>
                <Link className="next-page" style={{ visibility: `${visNext}` }} to={`/search/${keyw}/${Number(page) + 1}`}>
                    <h3>Next</h3>
                </Link>
            </div>
            <div className="anime-list">
                {isData ? <>
                    {animeList.map((animeDetails, i) =>
                        <div key={i} className={`${animeDetails.animeSlug}`}>
                            <div className={`${animeDetails.animeSlug}-img`}>
                                <Link to={`/anime/${animeDetails.animeSlug}`}>
                                    <img
                                        src={animeDetails.animeImg}
                                        alt={`${animeDetails.animeSlug}-img`}
                                    />
                                </Link>
                            </div>
                            <div className={`${animeDetails.animeSlug}-data anime-data`}>
                                <Link to={`/anime/${animeDetails.animeSlug}`}>
                                    <h2>{animeDetails.animeTitle}</h2>
                                </Link>
                                <h4>{animeDetails.status}</h4>
                            </div>
                        </div>
                    )}
                </> : <>
                    <h1>Loading..</h1>
                </>
                }
            </div>
        </>
    )
}

