import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Navbar from "./Navbar";
import SearchRoundedIcon from '@mui/icons-material/SearchRounded';
import "./css/SearchData.css"

export default function Details() {
    let [search, setSearch] = useState("")
    const [timeout, setTime] = useState(null);
    const [animeData, setAnimeData] = useState([]);

    const handleKeyUp = (e) => {
        clearTimeout(timeout);
        setTime(setTimeout(function () {
            searchAnime(search);
        }, 200))
    };

    function searchAnime(search) {
        fetch(`https://backend-server-ao58.onrender.com/search?keyw=${search}`).then(
            response => response.json()
        ).then(
            data => {
                search === "" ? setAnimeData([]) : setAnimeData(data);
            }
        )
    }

    useEffect(() => {
        searchAnime(search);
    }, [search])

    return (
        <>
            <Navbar />
            <div className="search-container">

                <div className="search-box">
                    <input
                        className="search"
                        id="search"
                        placeholder="Search"
                        value={search}
                        onChange={(e) => setSearch(e.target.value)}
                        onKeyUp={handleKeyUp}
                        required />
                    <SearchRoundedIcon className="search-icon" />
                </div>
                <div className="anime-list">
                    {animeData.map((animeDetails, i) => <div key={i} className={`${animeDetails.animeSlug}`}>
                        <div className={`${animeDetails.animeSlug}-img`}>
                            <Link to={`/anime/${animeDetails.animeSlug}`}>
                                <img
                                    src={animeDetails.animeImg}
                                    alt={`${animeDetails.animeSlug}-img`}
                                />
                            </Link>
                        </div>
                        <div className={`${animeDetails.animeSlug}-data anime-data`}>
                            <Link to={`/anime/${animeDetails.animeSlug}`}>
                                <h2>{animeDetails.animeTitle}</h2>
                            </Link>
                            <h4>{animeDetails.status}</h4>
                        </div>
                    </div>)}
                </div>
            </div>
        </>
    )
}

